/* Base */
body {
  font-size: 1em;
  font-family: "Oxygen", sans-serif;
  color: #404647;
  background-color: #F5F5F5; }

a {
  text-decoration: none;
  color: #3498DB; }

.no-touch a:hover {
  color: #258cd1; }

.container {
  width: 100%;
  max-width: 74em;
  margin: auto; }
  .container.container-main {
    padding-left: 24em;
    position: relative; }
    @media screen and (max-width: 992px) {
      .container.container-main {
        padding-left: 0; } }

.container-navigation {
  width: 24em;
  position: fixed;
  left: calc(50% - 37em);
  top: 0;
  height: 100%;
  z-index: 100; }
  @media screen and (max-width: 1184px) {
    .container-navigation {
      left: 0; } }
  @media screen and (max-width: 992px) {
    .container-navigation {
      left: -24em; } }

.container-content {
  width: 100%; }

.container-fullscreen {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%; }

.dialog-static {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto; }

/* Modules */
.button {
  line-height: 2.85714286em;
  height: 2.85714286em;
  padding: 0 2em;
  font-family: "Lato", sans-serif;
  font-size: 0.875em;
  background-color: transparent;
  color: #3498DB;
  cursor: pointer; }

.button-icon {
  padding-right: 1.25em; }

.button-emphasized {
  background-color: #2ECC71;
  color: #FAFAFA;
  -webkit-transition: background-color 200ms;
  -moz-transition: background-color 200ms;
  transition: background-color 200ms; }

.no-touch .button-emphasized:hover {
  background-color: #29b765; }

input {
  width: 100%;
  height: 2.5em;
  background-color: transparent;
  font-size: 1em;
  color: #404647;
  outline: none;
  font-family: "Oxygen", sans-serif; }
  input::-webkit-input-placeholder {
    color: #BDC3C7; }
  input::-moz-placeholder {
    color: #BDC3C7; }
  input:-moz-placeholder {
    color: #BDC3C7; }
  input:-ms-input-placeholder {
    color: #BDC3C7; }

label {
  user-select: none; }

.form-group {
  margin-bottom: 1em; }
  .form-group input {
    border-bottom: 1px solid #BDC3C7;
    -webkit-transition: border-bottom 200ms;
    -moz-transition: border-bottom 200ms;
    transition: border-bottom 200ms; }
    .form-group input:focus {
      border-bottom: 1px solid #3498DB; }

.form-group-icon-label::after {
  clear: both;
  content: "";
  display: table; }

.form-group-icon-label .icon-label {
  float: right;
  color: #BDC3C7;
  display: inline-block;
  width: 2em;
  text-align: center;
  font-size: 1.25em;
  line-height: 2em;
  -webkit-transition: color 200ms;
  -moz-transition: color 200ms;
  transition: color 200ms; }

.form-group-icon-label input {
  float: right;
  width: calc(100% - 2.5em); }
  .form-group-icon-label input:focus + .icon-label {
    color: #3498DB; }

h2 {
  text-align: center;
  font-size: 2em;
  margin-bottom: 0.5em;
  color: #5F696A; }

.material-light {
  background-color: #FAFAFA;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.2); }

/* Pages */
.auth-dialog {
  width: 20em;
  height: 26em;
  padding: 0 1em; }
  .auth-dialog .auth-logo {
    text-align: center;
    padding: 1em 0 0; }
    .auth-dialog .auth-logo img {
      height: 3em;
      width: 3em; }
  .auth-dialog h2 {
    margin-top: 0.5em; }
  .auth-dialog .auth-error, .auth-dialog .auth-success {
    color: #E74C3C;
    padding: 0.5em;
    line-height: 1.25em;
    height: 3.5em; }
  .auth-dialog .auth-success {
    color: #2ECC71; }
  .auth-dialog form {
    padding: 0.5em 1em 1.25em 0; }
    .auth-dialog form.auth-form-space {
      padding-top: 4em; }
  .auth-dialog .auth-buttons::after {
    clear: both;
    content: "";
    display: table; }
  .auth-dialog .auth-buttons button {
    float: right; }
  .auth-dialog .auth-remember-check {
    display: block;
    float: left; }
    .auth-dialog .auth-remember-check span {
      -webkit-transition: color 200ms;
      -moz-transition: color 200ms;
      transition: color 200ms;
      color: #E74C3C; }
      .auth-dialog .auth-remember-check span .icon-check {
        display: none; }
    .auth-dialog .auth-remember-check input {
      display: none; }
      .auth-dialog .auth-remember-check input:checked + span {
        color: #2ECC71; }
        .auth-dialog .auth-remember-check input:checked + span .icon-check {
          display: inline; }
        .auth-dialog .auth-remember-check input:checked + span .icon-cancel {
          display: none; }
  .auth-dialog .auth-option {
    text-align: center;
    padding: 1em 0;
    border-top: 1px solid #F5F5F5; }

.texture-overlay {
